<template>
  <section class="discbox"  @click="$emit('show-disc')">
    <div class="imagn" :class="{ paused : !playing}">
      <img 
      :src="`${changePlaySong.picUrl || changePlaySong.al.picUrl}?imageView=1&type=webp&thumbnail=120x0`" />
    </div>
  </section>
</template>

<script>
export default {
  props: {
    changePlaySong: Object,
    playing : Boolean
  },
};
</script>

<style lang="less" scoped>
@keyframes disc {
    from{
     transform: rotate(0deg);
    }
    to{
    transform: rotate(360deg);
    }
}
.discbox{
  height: 65vh;
  padding-top: 25vh;
  margin-bottom: 60px;
.imagn {
    width: 180px;
    height: 180px;
    border-radius: 50%;
    margin: auto;
    overflow: hidden;
  img {
  width: 100%;
  height: 100%;
  animation: disc 10s linear infinite;
  }
  &.paused{
    img{
      animation-play-state: paused;
    }
  }
}
}
</style>