<template>
<section class="playbar">
    <img 
    :src="`${changePlaySong.picUrl || changePlaySong.al.picUrl }?imageView=1&type=webp&thumbnail=60x0`"
     alt="">
     <h5>{{changePlaySong.name}}</h5>
     <div class="progress">
         <span 
         v-if="!playing"
         @click.stop="$emit('audio-play')"
         class="play-icon"
         ></span>
         <span
         class="pause-icon" 
         v-else
         @click.stop="$emit('audio-pause')"
         ></span>
         <canvas ref="canvas" width="40" height="40" ></canvas>
     </div>
     
     <div class="currentlist" @click.stop="$emit('show-list')"></div>
</section>
</template>
<script>
    export default {
        components:{
        },
         data() {
            return {
                showList:false
            }
        },
        props:{
            changePlaySong:Object,
            playing:Boolean,
            duration:Number,
            currentTime:Number,
            circlePercent:Number
            
        },
        
        methods: {
            drawCircle : function (n) {
                // 获取
                var canvas = this.$refs.canvas;
                var ctx = canvas.getContext("2d");
                // 清楚
                ctx.clearRect( 0, 0, 40, 40);
                // 颜色
                ctx.strokeStyle = "#dd001b";
                //开启画路径
                ctx.beginPath();
                //画圆
                ctx.arc(20 , 20 , 18 , -0.5 * Math.PI , (2 * n -0.5) * Math.PI);
                ctx.stroke();


                ctx.strokeStyle="#ccc"
                ctx.beginPath();
                ctx.arc(20, 20, 18, -0.5 * Math.PI, (2 * n - 0.5) * Math.PI, true);
                ctx.stroke();
            }
          
                 
        },
        created() {
            // theCurrentList () {
                
            // }
        },
         mounted() {
             this.drawCircle(this.circlePercent);
         },
        watch:{
            circlePercent : function (n, o) {
                // console.log(n);
                this.drawCircle(n)
            }
        }
    }
</script>

<style lang="less" >
.playbar{
     width: 100%;
     height: 50px;
     position: fixed;
     bottom: 0;
     left: 0;
     display: flex;
     padding: 5px 10px;
    background-color: white;
    align-items: center;
    img{
        border-radius: 50%;
        width: 40px;
        height: 40px;
    }
    h5{
        flex: 1;
        padding:  0 10px;
    }
    .progress{
        position: relative;
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        span{
            width: 10px;
            height: 10px;
            &.play-icon{
            &::after{
                content: "";
                width: 20px;
                height: 10px;
                display: block;
                border-top: 5px solid transparent;
                border-right: 12px solid transparent;
                border-bottom: 5px solid transparent;
                border-left: 8px solid #ccc;
                margin-left: 2px;
            }
            }
            &.pause-icon{
                display: flex;  
                &::after{
                content: "";
                width: 20px;
                height: 10px;
                display: block;
                border-left: 3px solid #ccc;
                margin-left: 1px;
            }
             &::before{
                content: "";
                width: 20px;
                height: 10px;
                display: block;
                border-left: 3px solid #ccc;
                margin-left: 1px;
            }
            }
        }
    }
    .currentlist{
        width: 28px;
        height: 28px;
        margin-left: 20px;
        background:url(../assets/列表.png) no-repeat;
        background-size:  28px;;
    }
    canvas{
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
    }
}
</style>