<template>
  <section>
      <div>
        <div class="headtop">
            <div></div> 
            <div>
                <span></span>
                <span></span>
            </div>
        </div>
        <h4 class="tttop">歌曲列表</h4>
        <ul v-if="detail">
            <HotList
                v-for="(item,index) in detail.tracks" 
                :key="item.id"
                :item="item"
                :index="index"
                @change-current-play-song="$emit('change-current-play-song', $event)"
                />
      </ul>
        <h4>精彩评论</h4>
      <ul>
            <CommentsList
            :item="item"
            v-for="item in comments"
            :key="item.commentId"
             />
      </ul>  
      </div>
  </section>
</template>

<script>
import HotList from '@/components/HotList.vue';
import CommentsList from '@/components/CommentsList.vue';
    export default {
        components:{
          HotList,
          CommentsList,
        },
        data() {
            return {
                detail:null,
                comments:[],
                // List:[]
            }
        },
        created() {
            // 获取歌单详情
            this.axios
            .get("http://music.kele8.cn/playlist/detail?id=" + this.$route.query.id)
            .then((res)=>{
                console.log(res.data.playlist);
                this.detail= res.data.playlist;
                this.List= res.data.playlist;

            });
             //获取歌单评论
             this.axios
             .get("http://music.kele8.cn/comment/playlist" ,{
                 params:{
                     id:this.$route.query.id,
                 }
             }).then((res)=>{
                //  console.log(res.data);
                 this.comments = res.data.comments;
             })
        },
    }
</script>

<style lang="less" scoped>
