<template>
  <div class="bigbox">
    <div class="box">
      <div class="search">
        <span><img src="../assets/search.png" /></span>
        <input
          type="text"
          placeholder=" 搜索歌曲、歌手、专辑"
          v-model="value"
          @input="changInput"
        />
      </div>
    </div>
    <div v-if="isShow" class="hotListbox">
      <h3>热门搜索</h3>
      <SearchHotList :hotList="hotList"
       @change-hot-play-song = "changHotPlaySong"
       />
    </div>
    <ul class="searchitem" v-else>
      <div class="searchhader">
        <span class="searchtop">搜索："{{ this.value }}"</span>
      </div>
      <SearchListItem :lists="lists" />
    </ul>

      <ul>
        <SearchEverList
        :changHotPlaySongobj="changHotPlaySongobj"
        />
      </ul>
  </div>
</template>
<script>
import SearchHotList from "@/components/SearchHotList.vue";
import SearchListItem from "@/components/SearchListItem.vue";
import SearchEverList from "@/components/SearchEverList.vue";
export default {
  components: {
    SearchHotList,
    SearchListItem,
    SearchEverList
  },
  data() {
    return {
      value: "",
      lists: [],
      hotList: [],
      isShow: true,
      changHotPlaySongobj: null
    };
  },
  computed: {},
  methods: {
    changInput: function () {
      if (this.value !== "") {
        this.isShow = false;
      } else {
        this.isShow = true;
      }
    },
    changHotPlaySong : function (payload) {
       this.changHotPlaySongobj = payload
    }
  },
  created() {
    this.axios
      .get("http://music.kele8.cn/search/hot/detail")
      .then((response) => {
        // console.log(response.data.data);
        this.hotList = response.data.data;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  watch: {
    value: function (newValue) {
      //  console.log(newValue);
      this.axios
        .get("http://music.kele8.cn/search?keywords=" + newValue )
        .then((response) => {
          // console.log(response.data.result.songs);
          this.lists = response.data.result.songs;
        
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  height: 45px;
  width: 98%;
  border-bottom: 1px solid rgba(231, 222, 222, 0.2);
  margin: auto;
}
.search {
  margin: 10px auto;
  width: 85%;
  height: 30px;
  background-color: #ebecec;
  border-radius: 30px;
  span {
    img {
      position: absolute;
      width: 22px;
      height: 22px;
      left: 38px;
      top: 135px;
    }
  }
  input {
    border: none;
    outline: none;
    width: 100%;
    height: 30px;
    line-height: 18px;
    background: rgba(0, 0, 0, 0);
    font-size: 14px;
    color: #333;
    padding-left: 30px;
    font-size: 12px;
  }
}
.bigbox {
  h3 {
    margin: 10px;
    font-size: 13px;
  }
  .searchhader {
    width: 100%;
    padding: 10px 5px;
    border-bottom: 1px solid rgba(231, 222, 222, 0.2);
    .searchtop {
      margin: 5px 2px;
      display: inline-block;
      color: #507daf;
    }
  }

  
}
</style>