<template>
  <section class="playpage">
    <div class="mask"
      :style="{backgroundImage : `url(${ changePlaySong.picUrl || changePlaySong.al.picUrl}?imageView=1&type=webp&thumbnail=120x0)`}"
      >
    </div>
    <PlayPageHeader 
    @update:showBar="$emit('update:showBar', true)"
    :changePlaySong="changePlaySong" 
    />
    <PlayPageDisc
     v-if="isShowDisc"
    :playing="playing"
    :changePlaySong="changePlaySong" 
   @show-disc="showDisc"
     />
     
    <PlayPageLyric
     v-else
      @show-lyric="showLyric"
      :parsedLyric="parsedLyric"
      :currentTime="currentTime"
      :duration="duration"
      :playing="playing"
      :currentLyricIndex="currentLyricIndex"
     />
    
    <PlayPageControl
    :duration="duration"
    :currentTime="currentTime"
    @updata:currentTime="$emit('updata:currentTime', $event)"
    :playing="playing"
    @audio-play = "$emit('audio-play')"
    @audio-pause = "$emit('audio-pause')"
    @prev-song = "$emit('prev-song')"
    @netx-song= "$emit('netx-song')"
    @show-list= "$emit('show-list')"
     />
  </section>
</template>

<script>
import PlayPageHeader from "../components/PlayPageHeader.vue";
import PlayPageDisc from "../components/PlayPageDisc.vue";
import PlayPageLyric from "../components/PlayPageLyric.vue";
import PlayPageControl from "../components/PlayPageControl.vue";

export default {
  components: {
    PlayPageHeader,
    PlayPageDisc,
    PlayPageLyric,
    PlayPageControl,
  },
  props: {
    circlePercent: Number,
    changePlaySong: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    parsedLyric : Array,
    currentLyricIndex :Number
  },
  data() {
      return {
          isShowDisc:true
      }
  },
  methods: {
    showDisc : function (){
      this.isShowDisc =false
    },
    showLyric : function () {
        this.isShowDisc = true
    }
  },
};
</script>

<style lang="less" scoped>
.playpage {
  width: 100%;
  height: 100vh;
  background: rgb(230, 173, 173);
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 20;
  .mask{
     position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
     transform: scale(1.5);
     filter: blur(15px) brightness(0.5);
     transform: scale(1.5);
      z-index: -1;
  }
}
</style>