<template>
  <section class="palypage-lyric">
    <h3 @click.stop="$emit('show-lyric')"></h3>
    <div class="ulbox">
      <ul ref="lyricBox" :style="{ marginTop: -offsetTop + 'px' }">
        <li
          :class="{ active: index == currentLyricIndex , paused : !playing }"
          v-for="(item, index) in parsedLyric"
          :key="index"
        >
          <span
            :style="{
                animationDuration : index >= parsedLyric.length -1
                ?(duration - item.time) / 2 +'s'
                :  parsedLyric[index + 1].time - item.time + 's',
            }"
          >{{ item.Text }}</span>
        </li>
      </ul>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    parsedLyric: Array,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    currentLyricIndex: Number,
  },
  data() {
    return {
      offsetTop: 0,
    };
  },
  watch: {
    currentLyricIndex: function () {
      var h = 0;
      this.$refs.lyricBox.querySelectorAll("li").forEach((element, index) => {
        if (index < this.currentLyricIndex) {
          h += element.offsetHeight;
        }
      });
      this.offsetTop = h;
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes mymove {
    from{
        background-position-x: -100%;
    }
    to{
        background-position-x: -200%;
    }
}
.palypage-lyric {
  padding-top: 30px;
  height: 65vh;
  overflow: hidden;
  margin-bottom: 60px;
  h3 {
    height: 50px;
    padding-left: 15px;
  }
  .ulbox {
    overflow: hidden;
    padding-top: 50px;
    transform: translateY(50px);
    transition: all 0.5s;

  li {
    color: white;
    display: flex;
    flex: 1;
    justify-content: center;
    // font-size: 14px;
    &.active {
      span{
          background: linear-gradient( to right , #ef52ae 50% , #b8ef22 55% );
          -webkit-text-fill-color: transparent;
          background-clip: text;
          background-size: 200%;
          background-position-x: 100%;
          animation: mymove  linear ;
      }
      &.paused{
          span{

              animation-play-state: paused;
          }
      }
    }
    span {
      padding-top: 5px;
      width: 80%;
      text-align: center;
    }
  }
    }
}
</style>