<template>
  <section>
    <RecommendTitle>推荐歌单</RecommendTitle>
    <ul class="card-row3">
      <PlayList 
      class="card"
      v-for="item in personalized.slice(0,6)" 
      :key="item.id" 
      :item="item"
       />
    </ul>
  
    <RecommendTitle>最新音乐</RecommendTitle>
    <ul>
       <MusicList
        v-for="item in newsongs" 
        :key="item.id"
        :item="item"
        @change-current-play-song="$emit('change-current-play-song', $event) ;$emit('change-current-play-song-list',newsongs)"
       />
    </ul>
  </section>
</template>
<script>
// @代表src
import RecommendTitle from "@/components/RecommendTitle.vue";
import PlayList from "@/components/PlayList.vue";
import MusicList from '@/components/MusicList.vue';
export default {
  components: {
    RecommendTitle,
    PlayList,
    MusicList,
  },
  data() {
    return {
      personalized: [],
       newsongs: []
    };
  },
  created() {
    //  console.log(this.axios);
    this.axios
      .get("http://music.kele8.cn/personalized")
      .then((response) => {
        // console.log(response.data.result);
        this.personalized = response.data.result;
      })
      .catch((error)=> {
        console.log(error);
      });
       this.axios
      .get("http://music.kele8.cn/personalized/newsong")
      .then((response) => {
        // console.log(response.data.result);
        this.newsongs = response.data.result;
      })
      .catch((error)=> {
        console.log(error);
      });
  },
};
</script>
<style lang="less" scoped>
.card-row3{
  display: flex;
  flex-wrap: wrap;
  .card{
     flex: 1 0 30%;
    &:nth-child(3n+2){
      margin: 0 3px;
    }
  }
}
</style>