<template>
  <footer class="control">
    <div class="progerss">
      <input
        type="range"
        min="0"
        :max="duration"
        @change="changeValue"
        @input="inputing = true"
        v-model="value"
      />
      <div class="line" :style="{ background: '#ccc' }"></div>
      <div
        class="dot"
        :style="{
          left: ((inputing ? value : currentTime) / duration) * 100 + '%',
          background: 'red',
        }"
      ></div>
    </div>

    <ul class="icons">
      <li class="moshi"></li>
      <li class="shangyiqu" @click="$emit('prev-song')"></li>
      <li class="qidong" v-if="!playing" @click.stop="$emit('audio-play')"></li>
      <li class="zanting" v-else @click.stop="$emit('audio-pause')"></li>
      <li class="xiayiqu" @click="$emit('netx-song')"></li>
      <li class="liebiao" @click.stop="$emit('show-list')"></li>
    </ul>
  </footer>
</template>

<script>
export default {
  props: {
    duration: Number,
    currentTime: Number,
    playing: Boolean,
  },
  data() {
    return {
      value: this.currentTime,
      inputing: false,
    };
  },
  methods: {
    changeValue: function (e) {
      // console.log(e.target.value);
      this.$emit("updata:currentTime", e.target.value);
    },
  },
};
</script>

<style lang="less" scoped>
.control {
  width: 100%;
  margin: auto;
  .progerss {
    position: relative;
    width: 70%;
    margin: auto;
    height: 10px;
    display: flex;
    align-items: center;
    input {
      width: 100%;
      position: absolute;
      top: -1px;
      left: 0;
      height: 100%;
      z-index: 1;
      opacity: 0;
    }
  }
  .line {
    background: white;
    width: 100%;
    height: 4px;
    border-radius: 10px;
  }
  .dot {
    width: 10px;
    height: 10px;
    background-color: white;
    border-radius: 50%;
    position: absolute;
    top: 0px;
    left: 0;
    z-index: 0;
    margin-left: -5px;
    box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.3);
  }
  .icons {
    display: flex;
    margin: 30px 0;
    justify-content: space-around;

    color: #fff;
    li {
      height: 40px;
      width: 40px;

      background-size: 100%;
    }
    .moshi {
      background: url(../assets/lie.png) no-repeat;
        background-size: 30px;
       margin-top: 10px;
      margin-left: 10px;
    }
    .shangyiqu {
      background-image: url(../assets/shang.png);
      margin-top: 5px;
    }
    .qidong {
       height: 50px;
      width: 50px;
      background-image: url(../assets/pased.png);
       margin-bottom: 10px;
    }
    .zanting {
      height: 50px;
      width: 50px;
      background-image: url(../assets/暂停.png);
      margin-bottom: 10px;
    }
    .xiayiqu {
      background-image: url(../assets/xia.png);
       margin-top: 5px;
    }
    .liebiao {
      text-align: center;
      background: url(../assets/列表.png) no-repeat;
      background-size: 30px;
       margin-top: 10px;
    }
  }
}
</style>