<template>
    <li 
    class="current-play-list-item"
    @click="$emit('change-current-play-song', item)"
    >
    <div class="palying-animate"
    :class="{ pused : !playing }"
    v-if="changePlaySong.id == item.id"
     >
        <span></span>
        <span></span>
        <span></span>
    </div>
        <div class="detail" :class="{ active : changePlaySong.id == item.id }">
            <span>{{item.name}}  -</span>
            <span class="detailtwo" v-for="artist in item.song ? item.song.artists: item.ar" :key="artist.id">{{artist.name}}</span>
        </div>
        <div class="delete" @click.stop="$emit('del-music', item.id)">X</div>
    </li>
</template>

<script>
    export default {
        props:{
            changePlaySong : Object,
            changeCurrentPlaySongList:Array,
            item:Object,
            playing:Boolean
        }
    }
</script>

<style lang="less" scoped>
li{
    &.current-play-list-item{
        display: flex;
        margin: 10px 5px 10px 15px;

        @keyframes bofang {
            from{
                height: 20%;
            }
            to{
                height: 80%;
            }
        }
    }
    .palying-animate{
        width: 25px;
        height: 25px;
        
        // background:green;
        display:flex;
        justify-content: space-around;
        align-items: flex-end;  
        padding: 3px;
        padding-bottom: 6px;
        span{
            width: 20%;
            height: 20%;
            background-color: red;
             animation: bofang 0.6s linear  infinite alternate;
            &:nth-child(1){
                animation-delay: 0s;
            }
            &:nth-child(2){
                animation-delay: -0.2s;
            }
            &:nth-child(3){
                animation-delay: -0.4s;
            }
        }

        &.pused{ 
            span{
                 animation-play-state: paused;
            }
     }
    }
    .detail{
        &.active{
            span{
                color: red;
            }
        }
        .detailtwo{
            font-size: 12px;
            padding-left: 5px;
        }
        flex: 1;
        padding-left: 5px;
    }
    .delete{
        padding-right: 15PX;
        font-weight: bold;
    }
}
</style>